.voice-section {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 60px 20px;
  text-align: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
 
}

.voice-section h1 {
  font-size: 2.5rem;
  margin-bottom: 10px;
  font-weight: bold;
  color: white;
}

.subtitle {
  font-size: 1.1rem;
  color: whitesmoke;
  margin-bottom: 50px;
}

.feature-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 40px;
  margin-bottom: 80px;
  text-align: left;
  flex-wrap: wrap;
}

.feature-row.reverse {
  flex-direction: row-reverse;
}

.feature-row img {
  width: 100%;
  max-width: 450px;
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
  transition: transform 0.3s ease;
}

.feature-row img:hover {
  transform: scale(1.03);
}

.feature-content {
  flex: 1;
  min-width: 300px;
}

.feature-content h3 {
  font-size: 1.8rem;
  color: white;
  margin-bottom: 15px;
}

.feature-content p {
  font-size: 1rem;
  color: wheat;
  line-height: 1.6;
  margin-bottom: 20px;
}

.voice-btn {
  padding: 12px 24px;
  background: #e91e63;
  color: white;
  border: none;
  border-radius: 30px;
  font-size: 1rem;
  cursor: pointer;
  font-weight: 500;
  transition: all 0.3s ease;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.15);
}

.voice-btn:hover {
  background: linear-gradient(135deg, #0056d2, #003d99);
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
}

.voice-btn:active {
  transform: translateY(0);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Responsive Styles */
@media (max-width: 768px) {
  .feature-row {
    flex-direction: column !important;
    text-align: center;
  }

  .feature-content {
    text-align: center;
  }

  .feature-content h3 {
    font-size: 1.5rem;
  }
}
