.memory-section {
  text-align: center;
  padding: 60px 20px;
  font-family: Arial, sans-serif;
  background-color: black;
}

.memory-section h1 {
  font-size: 2.5rem;
  margin-bottom: 10px;
  color: white;
}

.memory-section .subtitle {
  font-size: 1.1rem;
  color: wheat;
  margin-bottom: 40px;
}

.memory-grid {
  display: flex;
  justify-content: center;
  gap: 30px;
  flex-wrap: wrap;
}

.memory-card {
  background: #111827;
  width: 300px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(0,0,0,0.1);
  text-align: left;
  transition: transform 0.3s ease;
}

.memory-card:hover {
  transform: translateY(-5px);
}

.memory-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.card-content {
  padding: 15px;
}

.card-content h3 {
  font-size: 1.2rem;
  color: white;
  margin: 0 0 5px;
}

.card-content .date {
  display: block;
  font-size: 0.9rem;
  color: whitesmoke;
  margin-bottom: 10px;
}

.card-content p {
  font-size: 0.95rem;
  color: whitesmoke;
  line-height: 1.4;
}
