.body{
   background-color: black;
}
.features-section {
  display: flex;
  flex-direction: column;
  gap: 80px;
  padding: 60px 20px;
  max-width: 1200px;
  margin: auto;
  color: white;
  background-color: black;
}
.features-section  title{
  color: white;
}
.feature-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 50px;
  flex-wrap: wrap;
}

.feature-row.reverse {
  flex-direction: row-reverse;
}

.feature-image {
  flex: 1 1 500px;
}

.feature-image img {
  width: 100%;
  border-radius: 12px;
  object-fit: cover;
  box-shadow: 0 8px 20px rgba(0,0,0,0.15);
  transition: transform 0.4s ease;
}

.feature-image img:hover {
  transform: scale(1.05);
}

.feature-text {
  flex: 1 1 400px;
}

.feature-text h2 {
  font-size: 2rem;
  margin-bottom: 15px;
  font-family: 'Segoe UI', sans-serif;
  color: white;
}
.smoking-text {
  font-size: 2.5rem;
  font-weight: bold;
  color: white;
  position: relative;
  text-transform: uppercase;
  animation: smokeText 4s ease-in-out infinite;
  text-shadow: 0 0 10px rgba(255,255,255,0.6),
               0 0 20px rgba(255,255,255,0.4),
               0 0 30px rgba(255,255,255,0.2);
}

/* Add moving smoke around */
@keyframes smokeText {
  0% {
    text-shadow: 
      0 0 10px rgba(255,255,255,0.6),
      0 0 20px rgba(255,255,255,0.4),
      0 0 30px rgba(255,255,255,0.2);
    transform: translateY(0px);
    opacity: 1;
  }
  50% {
    text-shadow: 
      0 0 15px rgba(255,255,255,0.8),
      10px -10px 30px rgba(200,200,200,0.5),
      -10px 10px 40px rgba(180,180,180,0.3);
    transform: translateY(-5px);
    opacity: 0.9;
  }
  100% {
    text-shadow: 
      0 0 10px rgba(255,255,255,0.6),
      -10px -10px 30px rgba(220,220,220,0.3),
      10px 10px 40px rgba(160,160,160,0.2);
    transform: translateY(0px);
    opacity: 1;
  }
}

.feature-text p {
  font-size: 1rem;
  color: #555;
  line-height: 1.6;
  color: wheat;
}

.personalize-section {
  text-align: center;
  padding: 60px 20px;
  max-width: 1200px;
  margin: auto;
}

.personalize-section h1 {
  font-size: 2.5rem;
  margin-bottom: 10px;
  font-family: 'Segoe UI', sans-serif;
}

.personalize-section p {
  font-size: 1.1rem;
  color: #444;
  margin-bottom: 50px;
}

.personalize-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 40px;
  justify-items: center;
}

.grid-item img {
  width: 100%;
  max-width: 500px;
  border-radius: 8px;
  object-fit: cover;
  box-shadow: 0 6px 18px rgba(0,0,0,0.15);
  transition: transform 0.4s ease;
}

.grid-item img:hover {
  transform: scale(1.05);
}

/* Responsive */
@media (max-width: 768px) {
  .feature-row {
    flex-direction: column !important;
    text-align: center;
  }
  .feature-text h2 {
    font-size: 1.5rem;
  }
}
